import React from "react";
import { NavLink, Switch, Route } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";

function AboutUs() {
  return (
    <div>
      <Card className="text-center">
        <Card.Header>Working Details</Card.Header>
        <Card.Body>
          <Card.Title>About Us</Card.Title>
          <Card.Text>We are working on web devlopment.</Card.Text>
          <Card.Text>
            We are also working on mobile and cloud technology
          </Card.Text>
          <Button variant="primary">Go somewhere</Button>
        </Card.Body>
        <Card.Footer className="text-muted">2 days ago updated</Card.Footer>
      </Card>
    </div>
  );
}

function ContactUs() {
  return (
    <div class="container">
      <div class=" mt-5">
        <div class="row border-top border-bottom m-0">
          <div class="col-xl-4 col=lg-4 col-md-6 col-sm-12 border-right">
            <div class="d-flex">
              <div class="d-flex align-items-center"></div>
              <div class="mt-3 ml-3">
                <p class="m-0 font-weight-bold">Address</p>
                <p>123,Street name,city,your country</p>
              </div>
            </div>
          </div>

          <div class="col-xl-4 col=lg-4 col-md-6 col-sm-12 border-right">
            <div class="d-flex">
              <div class="d-flex align-items-center"></div>
              <div class="mt-3 ml-3">
                <p class="m-0 font-weight-bold">Call Us</p>
                <p>+123-456-789</p>
              </div>
            </div>
          </div>

          <div class="col-xl-4 col=lg-4 col-md-12 col-sm-12">
            <div class="d-flex">
              <div class="d-flex align-items-center"></div>
              <div class="mt-3 ml-3">
                <p class="m-0 font-weight-bold">Email</p>
                <span>
                  <p class="text-primary">info@companyname.com</p>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
function HomePage() {
  return (
    <div className="container bg-light">
      <h1 className="m-5">Home Page</h1>
      <p className="m-5 p-3">This is the firstpage Of An Website</p>
    </div>
  );
}

function NotFound() {
  return (
    <div className="container bg-light">
      <h1 className="m-5">404 Page not found</h1>
      <p className="m-5 p-3">
        This is the Wrong URL please enter valid keyword
      </p>
    </div>
  );
}

const Form = () => {
  return (
    <div>
      <Navbar bg="dark" variant="dark" mb-3>
        <Navbar.Brand href="#home">RouteExample </Navbar.Brand>
        <Navbar className="mr-auto m-2 p-3">
          <NavLink to="/">Home</NavLink>
          <span className="m-2">
            {" "}
            <NavLink to="/contact">Contact</NavLink>
          </span>
          <NavLink to="/company">Company</NavLink>{" "}
        </Navbar>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <HomePage />
        </Route>
        <Route path="/contact">
          <ContactUs />
        </Route>
        <Route path="/company">
          <AboutUs />
        </Route>

        <Route path="*">
          <NotFound />
        </Route>
      </Switch>
    </div>
  );
};

export default Form;
